<template>
  <dv-border-box-8
    ><div class="mydata2-box">
      <div class="data-title">
        <div class="box-left">
          <span style="color: #4ea4f3">▎</span>
          <span>停车时长分布</span>
        </div>
      </div>
      <div class="data-box" id="mydata6"></div></div
  ></dv-border-box-8>
</template>

<script>
// import Charts from '@jiaminghi/charts';
import ChartOptiion from '../common/ChartOption'
export default {
  data() {
    return {
      myChart:null
    };
  },
  mounted() {
    this.initCharts();
    // this.getChartData();
    window.addEventListener('resize', this.screenAdapter);
    this.screenAdapter();
  },
  destroyed() {
    window.removeEventListener('resize', this.screenAdapter);
  },
  methods: {
    initCharts() {
      const pieData = [
        {value: 77, name: '15分钟以下'},
        {value: 48, name: '15分钟-1小时'},
        {value: 22, name: '1-3小时'},
        {value: 30, name: '3小时以上'},
      ]
      if(!this.myChart){
        // 第一次加载假数据
        const option = ChartOptiion.stoptimeData(true,pieData)
        console.log('option',option)
        this.myChart = this.$echarts.init(document.getElementById('mydata6'));
        this.myChart.setOption(option);
      }else{
        // 请求数据真实数据填充位
        const option = ChartOptiion.stoptimeData(true,pieData)
        this.myChart = this.$echarts.init(document.getElementById('mydata6'));
        this.myChart.setOption(option);
      }
    },
    screenAdapter(){
      this.myChart.resize();
    }
  },
}
</script>

<style scoped lang='less'>
.mydata2-box {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .data-title {
    width: 100%;
    height: 10%;
    display: flex;
    padding: 0.6rem 1rem 0 1rem;
    box-sizing: border-box;
    color: #fff;
    font-size: 1.4rem;
    justify-content: space-between;
    .box-right {
      span {
        &:last-child {
          margin-left: 1rem;
        }
      }
    }
  }
  .data-box {
    flex: 1;
  }
}
</style>